
const OnRender = {
  MOUNT: 'MOUNT',
  SELECT: 'SELECT',
  TOGGLE: 'TOGGLE',
  DROP: 'DROP'
};

const onRenderCall = (tree, cause, context) => {
  if (!tree.props.onRender || typeof tree.props.onRender !== 'function') return;
  else if (!OnRender[cause]) throw new Error('Unknown cause: ' + cause);

  const
    event = {cause, context},
    treeArg = {
      dom: tree.refs.tree
    };

  tree.props.onRender(event, treeArg);
};

export {onRenderCall as default, OnRender}



